import Vue from 'vue'
import Vuex from 'vuex'
// 2. 使用 Vuex 插件
Vue.use(Vuex);
import { setUserInfo, removeUserInfo } from '@/utils/catheUtils';

// 3. 创建 store 仓库
const store = new Vuex.Store({
    state: {
        userInfo: {}, // 用户个人资料
        newMsg: [],
        newFir: false,
    },
    mutations: {
        updateUserInfo(state, user){
            state.userInfo = user // 个人数据对象
            setUserInfo(user)
        },
        loginin(state, user) {
            state.userInfo = Object.assign({}, user) // 个人数据对象
            setUserInfo(user); // 设置整个provider对象的
        },
        logout(state) {
            state.userInfo = {};
            removeUserInfo();
        },
        clearNewMsg(state,tel) {
            var arr = []
           
            if(state.newMsg && (state.newMsg).length > 0) {
                for(var i = 0; i < (state.newMsg).length; i++) {
                    if((state.newMsg)[i] != tel) {
                        arr.push(state.newMsg[i])
                    }
                }
            }
            state.newMsg = []
            state.newMsg = Object.assign([], arr)
        },
        addNewMsg(state,tel) {
            if(state.newMsg) {
                if(tel != state.userInfo.tel) {
                state.newMsg.push(tel)
                }
            }
        },
        changMsg(state, type) {
            state.newFir = type
        }
    },
});

// 4. 向外提供实例化的 store 对象, 用于注入整个应用
export default store;