<template>
  <div id="app">
    <navbar
      v-if="$route.meta.showHead"
      class="navbar_color"
      :title="topLitle"
      :arrow="$route.meta.arrow"
      :right="$route.meta.right"
      :left="city"
      :onClickRight="godo"
      :onClickLeft="goback"
    ></navbar>
    <transition name="fade" mode="out-in" v-if="$route.meta.keepAlive">
      <keep-alive>
        <router-view tag="div" v-if="$route.meta.keepAlive" class="content_app" />
      </keep-alive>
    </transition>
    <transition name="fade" mode="out-in" v-if="!$route.meta.keepAlive">
      <router-view tag="div" v-if="!$route.meta.keepAlive" class="content_app" />
    </transition>
    <tabbar v-if="$route.meta.showTab"></tabbar>
  </div>
</template>

<script>
import tabbar from "@/components/tabbar/tabbar";
import navbar from "@/components/navbar/navbar";
import { mapState } from 'vuex'
import AMap from "AMap";
import { Toast } from 'vant';

export default {
  name: "App",
  components: {
    tabbar,
    navbar
  },
  data() {
    return {
      city: ''
    };
  },
  computed: {
    ...mapState(['userInfo']),
    topLitle() {
      if(this.$route.path === '/chatinfo') {
        return this.$route.query.id.name
      } else {
        return this.$route.meta.title
      }
    },
    route() {
      return this.$route.path
    }
  },
  watch: {
    route(v) {
      let that = this
      if(v === '/service') {
        AMap.plugin('AMap.CitySearch', function () {
          var citySearch = new AMap.CitySearch()
          citySearch.getLocalCity(function (status, result) {
            if (status === 'complete' && result.info === 'OK') {
            // 查询成功，result即为当前所在城市信息
             AMap.plugin('AMap.Weather', function() {
                var weather = new AMap.Weather();
                //查询实时天气信息, 查询的城市到行政级别的城市，如朝阳区、杭州市
                weather.getLive(result.city, function(err, data) {
                    if (!err) {
                      that.city = result.city + data.weather + '/' + data.temperature + '℃'
                    }
                });
             })
            }
          })
        })
      } else {
        that.city = ''
      }
    }
  },
  methods: {
    goback() {
      var routePath = this.$route.path
      if(routePath === '/chat'|| routePath === '/lianxi' || routePath === '/service' || routePath === '/mine') {
      } else {
        this.$router.go(-1);
      }
    },
    godo() {
      if(this.$route.path === '/lianxi') {
        this.$router.push('/addFriend')
      }
    }
  },
  mounted() {
    let that = this
    //重新连接
    this.$socket.removeAllListeners('reconnect');
    this.$socket.on('reconnect', () =>{
      if(that.userInfo && that.userInfo.tel) {
        this.$socket.emit('reduankai', that.userInfo.tel)
        var content = `重新连接成功`
            this.$notify({
              type: 'success',
              message: content,
              duration: 2000
        });
      }
    })
  }
};
</script>

<style>

* {
  margin: 0;
  padding: 0;
}
#app {
  font-family: "Noto";
}
.content_app {
  padding-top: 46px;
  height: calc(100vh - 96px);
  overflow: scroll;
}
.navbar_color {
  background-color: #00887a !important;
  z-index: 999 !important;
}
.van-nav-bar__title {
  color: white !important;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
} 
.van-nav-bar__left >>> .van-nav-bar__text {
  font-size: 12px;
}
</style>
