import Vue from 'vue'
import Router from 'vue-router'

const chat = () => import('@/views/first/chat.vue')
const lianxi = () => import('@/views/second/lianxi.vue')
const service = () => import('@/views/third/service.vue')
const mine = () => import('@/views/fourth/mine.vue')
const login = () => import('@/views/login/login.vue')
const forgetpwd = () => import('@/views/forgetpwd/forgetpwd.vue')
const register = () => import('@/views/register/register.vue')
const xieyi = () => import('@/views/xieyi/xieyi.vue')
const sousuo = () => import('@/views/second/childComp/sousuo.vue')
const getFriend = () => import('@/views/second/childComp/getFriend.vue')
const addFriend = () => import('@/views/second/childComp/addFriend.vue')
const about = () => import('@/views/fourth/childComp/about.vue')
const userinfo = () => import('@/views/fourth/childComp/userinfo.vue')
const changePwd = () => import('@/views/fourth/childComp/changePwd.vue')
const password = () => import('@/views/fourth/childComp/password.vue')
const mibaoSet = () => import('@/views/fourth/childComp/mibaoSet.vue')
const chatinfo = () => import('@/views/first/childComp/chatinfo.vue')

Vue.use(Router)
const routes = [{
    path: '/',
    //redirect重定向，打开页面的默认路径
    redirect: '/login'
  },
  {
    //chat页面
    path: '/chat',
    component: chat,
    meta: {
      keepAlive: false,
      showTab: true,
      showHead: true,
      title: '聊天'
    }
  },
  {
    //联系人页面
    path: '/lianxi',
    component: lianxi,
    meta: {
      keepAlive: false,
      showTab: true,
      showHead: true,
      right: '添加',
      title: '联系人'
    }
  },
  {
    //服务页面
    path: '/service',
    component: service,
    meta: {
      keepAlive: false,
      showTab: true,
      showHead: true,
      title: '服务'
    }
  },
  {
    //我的页面
    path: '/mine',
    component: mine,
    meta: {
      keepAlive: false,
      showTab: true,
      showHead: true,
      title: '我的'
    }
  },
  {
    //登录页面
    path: '/login',
    component: login,
    meta: {
      keepAlive: false,
      showTab: false,
      showHead: false,
      arrow: false,
      title: '登录'
    }
  },
  {
    //忘记密码页面
    path: '/forgetpwd',
    component: forgetpwd,
    meta: {
      keepAlive: false,
      showTab: false,
      showHead: false,
      arrow: false,
      title: '忘记密码'
    }
  },
  {
    //注册页面
    path: '/register',
    component: register,
    meta: {
      keepAlive: false,
      showTab: false,
      showHead: false,
      arrow: false,
      title: '注册'
    }
  },
  {
    //用户协议页面
    path: '/xieyi',
    component: xieyi,
    meta: {
      keepAlive: false,
      showTab: false,
      showHead: true,
      arrow: true,
      title: '协议'
    }
  },
  {
    //搜索已有联系人页面
    path: '/sousuo',
    component: sousuo,
    meta: {
      keepAlive: false,
      showTab: false,
      showHead: true,
      arrow: true,
      title: '搜索'
    }
  },
  {
    //新朋友页面
    path: '/getFriend',
    component: getFriend,
    meta: {
      keepAlive: false,
      showTab: false,
      showHead: true,
      arrow: true,
      title: '新朋友'
    }
  },
  {
    //添加朋友页面
    path: '/addFriend',
    component: addFriend,
    meta: {
      keepAlive: false,
      showTab: false,
      showHead: true,
      arrow: true,
      title: '添加新朋友'
    }
  },
  {
    //关于我们页面
    path: '/about',
    component: about,
    meta: {
      keepAlive: false,
      showTab: false,
      showHead: true,
      arrow: true,
      title: '关于我们'
    }
  },
  {
    //个人资料页面
    path: '/userinfo',
    component: userinfo,
    meta: {
      keepAlive: false,
      showTab: false,
      showHead: true,
      arrow: true,
      title: '个人资料'
    }
  },
  {
    //密保设置页面
    path: '/mibaoSet',
    component: mibaoSet,
    meta: {
      keepAlive: false,
      showTab: false,
      showHead: true,
      arrow: true,
      title: '密保设置'
    }
  },
  {
    //密码设置页面
    path: '/password',
    component: password,
    meta: {
      keepAlive: false,
      showTab: false,
      showHead: true,
      arrow: true,
      title: '密码设置'
    }
  },
  {
    //更改密码页面
    path: '/changePwd',
    component: changePwd,
    meta: {
      keepAlive: false,
      showTab: false,
      showHead: true,
      arrow: true,
      title: '更改密码'
    }
  },
  {
    //聊天内容页面
    path: '/chatinfo',
    component: chatinfo,
    meta: {
      keepAlive: false,
      showTab: false,
      showHead: true,
      arrow: true,
      title: '聊天'
    }
  }
]

const router = new Router({
  //routes配置router和路径属性
  routes,
  mode: 'hash'
  // linkActiveClass:'active'
})

//将这个router对象传入数据中
export default router //导出