<template>
  <van-tabbar class="tabbar" route v-model="active" active-color="#00887a" inactive-color="#c9c9c9" fixed safe-area-inset-bottom>
    <van-tabbar-item
      v-for="(item, index) in list"
      :key="index"
      replace
      :dot="((newMsg.length > 0 && index === 0) || (newFir && index === 1)) ? true :false "
      :to="item.path"
    >
      <span>{{item.title}}</span>
      <img slot="icon" slot-scope="props" :class="(item.index == 1 || index.index == 2) ? 'wd' : ''" :src="props.active ? item.active : item.inactive" />
    </van-tabbar-item>
  </van-tabbar>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { Toast } from 'vant';
export default {
  name: "tabbar",
  data() {
    return {
      active: 0,
      list: [
        {
          title: "聊天",
          path: "/chat",
          active: require("@/assets/tabbar/chat_activetwo.png"),
          inactive: require("@/assets/tabbar/chat.png")
        },
        {
          title: "联系人",
          path: "/lianxi",
          active: require("@/assets/tabbar/lianxi_activetwo.png"),
          inactive: require("@/assets/tabbar/lianxi.png")
        },
        {
          title: "服务",
          path: "/service",
          active: require("@/assets/tabbar/service_activetwo.png"),
          inactive: require("@/assets/tabbar/service.png")
        },
        {
          title: "我的",
          path: "/mine",
          active: require("@/assets/tabbar/set_activetwo.png"),
          inactive: require("@/assets/tabbar/set.png")
        }
      ]
    };
  },
  computed: {
    ...mapState(["userInfo","newMsg","newFir"]),
  },
  methods: {
    ...mapMutations(['clearNewMsg','addNewMsg','changMsg']),
  },
  mounted() {}
};
</script>

<style scoped>
.active_tab img {
  width: 26px;
  height: 26px;
}
.van-tabbar-item >>> .van-tabbar-item__text {
  transform: scale(0.7);
}
.van-tabbar-item >>> .van-tabbar-item__icon {
  transform: scale(1.2);
}
.tabbar .van-tabbar-item:nth-of-type(2)  img {
  width:21.6px;
  height:21.6px
}
.tabbar .van-tabbar-item:nth-of-type(3)  img {
  width:21.6px;
  height:21.6px
}
</style>