new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
// 路由验证
router.beforeEach((to, from, next) => {
  // to and from are both route objects. must call `next`.
    var arr = JSON.parse(JSON.stringify(store.state.userInfo))
    var path = ['/login', '/register','/xieyi','/forgetpwd','/chat']
    if(path.indexOf(to.path) == -1 && !arr.tel) {
      Toast('账号需要重登')
      return next('/login')
    }
  next()
})